.backgroundPoster{
    height: 75vh;
}

.backgroundPoster__overlay {
    position: absolute;
    bottom: 0px;
    height: 100%;
    display: flex;
    width: 100%;
    justify-content: center;
    background-image: linear-gradient(rgb(0,0,0,0) -80%, #0d0d0d 100%);
    opacity: 1;
    transition: opacity .3s;
}

.hero__title {
    font-weight: 700;
    margin-bottom: 0.4rem;
    text-align: left;
    text-shadow: 0 1px 6px #00000029;
}

.hero__stats {
    font-size: 1.4rem;
    margin-bottom: 1rem;
    text-align: left;
}

.hero__rating {
    margin-bottom: 0.5rem;
    display: block;
}

.hero__description {
    line-height: 1.5;
    text-align: left;
    margin-bottom: 1.4rem;
}

.img__container img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    border-radius: 12px;
    box-shadow: 0 3px 20px #00000029;
    outline: 1px solid #ffffff29;
}

@media only screen and (max-width: 1366px) {
    .backgroundPoster img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

@media only screen and (max-width: 900px) {
    .backgroundPoster {
        min-height: 45vh;
    }

    .backgroundPoster img {
        height: 100%;
    }

}

@media only screen and (max-width: 600px) {
    .backgroundPoster {
        height: 80vh;
    }

    .carousel .slide img, .carousel img {
        width: auto;
    }

    .backgroundPoster__overlay {
        padding: 2rem 0;
    }
}